.video-banner {
  background-color: rgba(26, 26, 26, 0.5);
  height: 100px;
  border: 1px solid black; }
  .video-banner a {
    position: absolute;
    left: 50%;
    -webkit-transform: translateX(-50%);
        -ms-transform: translateX(-50%);
            transform: translateX(-50%);
